<template>
<div>
  <div>
  <el-input
    type="textarea"
    placeholder="base64"
    v-model="ParserUrl"
    rows="10"
    show-word-limit
  />
</div>
  <div style="margin-bottom: 10px"></div>
  <div v-if="results">
    <el-input
      type="textarea"
      placeholder=""
      v-model="ParserList"
      rows="10"
      show-word-limit
    />
  </div>
  <div style="margin-bottom: 10px"></div>
  <el-button
    style="position: relative;left: 50%;transform: translate(-50%)"
    @click="isSubAddress2"
  >
  </el-button>
</div>
</template>

<script>
import { DecodeSub } from '@/api/sub'

export default {
  name: 'MyParser',
  data () {
    return {
      ParserUrl: '',
      ParserList: '',
      results: false
    }
  },
  methods: {
    async isSubAddress2 () { // 
      // HTTPHTTPS
      const regex = /(http|https):\/\/[^\s]+/g
      const matches = this.ParserUrl.match(regex)
      // 
      if (matches === null) return this.$message.error('')
      const { code, msg } = await DecodeSub({ urls: matches })
      if (code === 200) {
        this.ParserList = msg.join('\n')
        this.results = true
      }
      if (code === 400) {
        this.$message.error(msg)
      }
    }

  }
}
</script>

